import UserList from '../UserList';
import styles from './styles/Home.module.css';

export default function ApiUsersTab({ 
  users, 
  loading, 
  onFetchUsers, 
  onSaveAllUsers, 
  onSaveUser, 
  onUpdateSavedUsers 
}) {
  return (
    <div>
      <div className={styles.controls}>
        <button onClick={onFetchUsers} disabled={loading}>
          {loading ? 'Carregando...' : 'Buscar novos usuários'}
        </button>
        {users.length > 0 && (
          <button onClick={onSaveAllUsers} className={styles.saveAll}>
            Salvar todos os usuários no CSV
          </button>
        )}
      </div>
      
      {users.length > 0 && (
        <p className={styles.info}>{users.length} usuários</p>
      )}
      
      <UserList 
        users={users} 
        onSave={onSaveUser}
        onUpdate={onUpdateSavedUsers}
      />
    </div>
  );
}